.topbar{
   width: 100%;
   height: 5%;
   position: fixed;
   z-index: 1;
   top:0;
   overflow-y: hidden;
   background-color: #e1e1d0;
}

.sidebar{
   height: 100%;
   width: 11%;
   position: fixed;
   z-index: 1;
   top: 0;
   left: 0;
   overflow-x: hidden;
   background-color: #e1e1d0;
   
   padding-top: 20px;
}

.links{
   color: black;
   text-decoration: none;
   display: block;
   font-size: 1rem;
   padding: 10px;
   padding-bottom: 10px;
}

.links:hover{
   text-decoration: underline;
}

.links:visited{
   text-decoration: none;
}

.links:active{
   font-weight: bold;
}